<template>
  <nav>
    <v-toolbar :color="colorTheme" dark>
      <v-app-bar-nav-icon @click="isDrawer = !isDrawer"></v-app-bar-nav-icon>
      <v-toolbar-title>{{ pageTitle }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <div class="hidden-sm-and-down">Selamat datang, {{ user.name }}</div>
      <v-btn @click="logout" text>
        <span>Logout</span>
        <v-icon>mdi-exit-to-app</v-icon>
      </v-btn>
    </v-toolbar>

    <v-navigation-drawer v-model="isDrawer" :color="colorTheme" absolute temporary dark>
      <v-list nav>
        <v-list-item two-line>
          <v-list-item-avatar>
            <v-img src="https://randomuser.me/api/portraits/women/85.jpg"></v-img>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>{{ user.name }}</v-list-item-title>
            <v-list-item-subtitle>Siswa</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <v-list-item v-for="(page, index) in pages" :key="index" link :to="page.routerTo">
          <v-list-item-icon>
            <v-icon>{{ page.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ page.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>
<script>
import authMixin from "../mixins/authMixin";
import { EventBus } from "@/bus";

export default {
  name: "UserNavbar",
  props: {
    pageTitle: String,
  },
  data: () => ({
    isDrawer: false,
    colorTheme: "primary",
    user: {},
    pages: [
      {
        name: "Dasbor",
        icon: "mdi-view-dashboard",
        routerTo: { name: "Home" },
      },
      { name: "Profil", icon: "mdi-account", routerTo: { name: "Profile" } },
    ],
  }),
  computed: {
    getUserData() {
      return this.$store.state.auth.user;
    },
  },
  methods: {
    async logout() {
      let logoutResponse = await this.$store.dispatch("auth/logout");
      if (logoutResponse.status === 200) {
        this.redirectLogin("Berhasil logout");
      }
    },
  },
  created() {
    this.user = this.getUserData;

    EventBus.$on("onProfileUpdated", () => {
      this.user = this.getUserData;
    });

    EventBus.$on("onSessionEnd", (message) => {
      this.$store.state.auth.user = null;
      this.$store.state.auth.status.isLoggedIn = false;
      localStorage.clear();
      this.redirectLogin(message);
    });
  },
  mixins: [authMixin],
};
</script>